video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  background: url('/images/homebg.jpg') center center/cover no-repeat; 
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  animation: changeBackgroundImage 13s infinite;
  margin-top: 12rem;
}
@keyframes changeBackgroundImage {
  0% {
    background: url('/images/homebg.jpg') center center/cover no-repeat;
  }
  25% {
    background: url('/images/homebg1.jpg') center center/cover no-repeat;
  }
  75% {
    background:url('/images/homebg2.jpg') center center/cover no-repeat;
  }
  100% {
    background:url('/images/homebg4.jpg') center center/cover no-repeat;
  }
}

.hero-container > span {
  color: #fff;
  font-size: 100px;
  margin-top: 30px;
  margin-left: 50px;
}

.hero-container > p {
  margin-top: 8px;
  color: #f66840 !important; 
  font-size: 40px;
  font-weight: bold;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    margin-left: 50px;
    text-shadow: 5px -1px 2px rgba(0,0,0,0.9);
}

.hero-btns {
  margin-top: 32px;
  margin-left: 40px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container  {
    font-size: 70px;
    align-items: center;
    margin-top: 0rem;
  
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
    
  } 
  .hero-container {
    margin-top: 0;
  }

  .hero-container > p {
    font-size: 25px;
    text-align: center;
    margin-top: 30px;
  }
  .hero-container > span {
    font-size: 30px;
    text-align: center;
    font-weight: 700;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .hero-btns {
    width: 50%;
  }
}
